<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process174"></a>

<p><br></p>
<p><span class="rvts17">神灯与愿望（</span><span class="rvts50">Wishful Thinking</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:190,Y:13</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：灯神（</span><span class="rvts20">Djinn</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">召唤神灯，并通过它的测试</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1181.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">在浮木城西边城外的一处沙滩上，有一盏金闪闪的神灯，摩擦它可以召唤出灯神。然而悲剧的是，这个灯神和</span><span class="rvts20"></span><span class="rvts21">王大锤</span><span class="rvts20"></span><span class="rvts21">里面的一样，需要你帮它实现愿望才行。</span>
<br><span class="rvts20"></span><span class="rvts21">我是神不是人，我要的其实很单纯</span><span class="rvts20"></span><span class="rvts21">。这个灯神会给你出一段非常长的并且稀奇古怪的对话，一定要注意答案的选择，一言不合很有可能遭遇杀身之祸。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1182.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">具体的答案如下。</span>
<br><span class="rvts21">未通过：</span>
<br><span class="rvts21">一旦未通过</span><span class="rvts20"></span><span class="rvts21">智力（</span><span class="rvts20">Intelligence</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">灵巧（</span><span class="rvts20">Finess</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">和</span><span class="rvts20">"</span><span class="rvts21">神秘（</span><span class="rvts20">Mystic</span><span class="rvts21">）</span><span class="rvts20">"</span><span class="rvts21">的段子，那么灯神将立刻进入战斗，打败灯神，可以获得经验和一盏空灯。</span>
<br><span class="rvts20"></span>
<br><span class="rvts81">坏愿望：</span>
<br><span class="rvts21">除了通过</span><span class="rvts20"></span><span class="rvts21">智力（</span><span class="rvts20">Intelligence</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">灵巧（</span><span class="rvts20">Finess</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">和</span><span class="rvts20">"</span><span class="rvts21">神秘（</span><span class="rvts20">Mystic</span><span class="rvts21">）</span><span class="rvts20">"</span><span class="rvts21">的段子，但并未通过其它段子，那么灯神将给你一个坏的愿望。</span>
<br><span class="rvts20">1. </span><span class="rvts21">希望得到权利（</span><span class="rvts20">Power</span><span class="rvts21">），灯神会对你进行闪电打击，造成伤害和击倒效果。</span>
<br><span class="rvts20">2. </span><span class="rvts21">希望得到财富（</span><span class="rvts20">Wealth</span><span class="rvts21">），将得到灯神的护身符（价值</span><span class="rvts20">4770</span><span class="rvts21">金）。然而，该护身符具有</span><span class="rvts20"></span><span class="rvts21">被偷盗</span><span class="rvts20"></span><span class="rvts21">的标签，之后所有的商人都怀疑你偷了他们的东西，并要求查看你的背包。</span>
<br><span class="rvts20">3. </span><span class="rvts21">希望得到知识（</span><span class="rvts20">Knowledge</span><span class="rvts21">），将得到一本儿童故事书，里面充满了恶语。</span>
<br><span class="rvts20">4. </span><span class="rvts21">希望天下所有的敌人从眼前消失（</span><span class="rvts20">Wipe out the Enemy</span><span class="rvts21">），和整个队伍的其它成员将会得到永久的视野致盲。</span>
<br><span class="rvts20"></span>
<br><span class="rvts81">好愿望：</span>
<br><span class="rvts21">除了通过</span><span class="rvts20"></span><span class="rvts21">智力（</span><span class="rvts20">Intelligence</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">灵巧（</span><span class="rvts20">Finess</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">和</span><span class="rvts20">"</span><span class="rvts21">神秘（</span><span class="rvts20">Mystic</span><span class="rvts21">）</span><span class="rvts20">"</span><span class="rvts21">的段子，并通过了其它的段子，那么灯神将给你一个好的愿望。</span>
<br><span class="rvts20">1. </span><span class="rvts21">希望得到权利（</span><span class="rvts20">Power</span><span class="rvts21">），得到一把史诗剑叫做灯神的短弯刀（</span><span class="rvts20">Djinn's Scimitar</span><span class="rvts21">）。</span>
<br><span class="rvts20">2. </span><span class="rvts21">希望得到财富（</span><span class="rvts20">Wealth</span><span class="rvts21">），得到灯神的护身符（非偷窃版本），该护身符没有任何属性，但价值</span><span class="rvts20">4770</span><span class="rvts21">金。</span>
<br><span class="rvts20">3. </span><span class="rvts21">希望得到知识（</span><span class="rvts20">Knowledge</span><span class="rvts21">），得到一本</span><span class="rvts20">3</span><span class="rvts21">级的随机派系技能书。</span>
<br><span class="rvts20">4. </span><span class="rvts21">希望天下所有的敌人从眼前消失（</span><span class="rvts20">Wipe out the Enemy</span><span class="rvts21">），得到终极水元素技能卷轴雷雨冰雹（</span><span class="rvts20">HailStorm</span><span class="rvts21">）。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">另外，如果使用角色特定的语言（比如塞碧莉，费恩）许愿，灯神会拒绝并要求再次许愿。如果使用智慧（</span><span class="rvts20">Wit</span><span class="rvts21">）属性与它对话并成功通过，那么它将瞬间消失。</span>
<br><span class="rvts21">如果与灯神发生了打斗，一旦取得胜利，将会获得一定的经验值，并获得神灯一盏（价值</span><span class="rvts20">2000</span><span class="rvts21">金）。</span>
<br><span class="rvts21">灯神的题目要求等级与角色等级密切相关（智力，灵巧等），奖励的物品等级也随角色的等级。因此理论上来说，可以先不搓出来灯神，而携带在身上，等到高等级的时候再搓出来进行任务。这样还可以获得更多的经验。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
